<template>
    <div style="text-align: center;padding-top: 44px;background: #fff;height: 100vh">
        <ul class="mui-table-view" style="line-height: 40px">
            <li class="mui-table-view-cell">
                <router-link to="/resetpass" class="mui-navigate-right">修改密码</router-link>
            </li>
            <li class="mui-table-view-cell">
                <router-link to="/upphoto" class="mui-navigate-right">修改头像</router-link>
            </li>
            <li class="mui-table-view-cell">
                <router-link to="/looklog" class="mui-navigate-right">查看日志</router-link>
            </li>
        </ul>
        <ul class="mui-table-view" style="width:100%;position:absolute;bottom:40px;left:0;line-height: 40px">
            <li class="mui-table-view-cell" style="text-align: center;">
                <a href="javascript:;" id='exit' @click="logout">退出</a>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        methods:{
            logout(){
                sessionStorage.removeItem("indexLogin");
                mui.toast('退出成功',{ duration:'long', type:'div' })
                this.$router.push("/")
            }
        }
    }
</script>

<style scoped>

</style>


