<template>
    <div>
        <header class="mui-bar mui-bar-nav">
            <router-link to="/" class="mui-icon mui-icon-home mui-pull-left"></router-link>
            <h1 class="mui-title">登陆</h1>
        </header>
        <div class="mui-content">
            <form id='login-form' class="mui-input-group">
                <div class="mui-input-row">
                    <label>工号</label>
                    <input id='account' type="text" class="mui-input-clear mui-input" placeholder="请输入工号" v-model="number">
                </div>
                <div class="mui-input-row">
                    <label>密码</label>
                    <input id='password' type="password" class="mui-input-clear mui-input" placeholder="请输入密码" v-model="pass">
                </div>
            </form>
            <div class="mui-content-padded">
                <button id='login' class="mui-btn mui-btn-block mui-btn-primary" @click="submit">登录</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                number:"",
                pass:""
            }
        },
        methods:{
            submit(){
                var that=this;
                fetch("/member/checkLogin",{
                    method:"post",
                    headers:{"content-Type":"application/x-www-form-urlencoded"},
                    body:"number="+this.number+"&pass="+this.pass
                }).then(function (e) {
                    return e.json()
                }).then(function (e) {
                    if(e.message){
                        mui.alert("登陆错误")
                        that.number=""
                        that.pass=""
                    }else{
                        sessionStorage.indexLogin=JSON.stringify(e[0]);
                        that.$router.go(-1)
                    }

                })
            }
        }
    }
</script>
<style scoped>

</style>


