<template>
    <ul class="mui-table-view">
        <pullDown :callback="fn" :autoLoad="true">
            <li class="mui-table-view-cell" v-for="item in datas">
                <a class="mui-navigate-right">{{item.title.substr(0,18)}}...</a>
            </li>
        </pullDown>
    </ul>
</template>

<script>
    import pullDown from "@/components/pullDown"
    export default {
        name:"customNews",
        components:{pullDown},
        data(){
            return {
                datas:[],
                pages:0
            }
        },
        created(){

        },
        methods:{
            fn(obj){
                var that=this;
                if(this.$root.routerPlace){
                    this.datas=this.$root.datas;
                    this.$root.page= this.$root.page?++this.$root.page:this.page;
                    this.page=this.$root.page;
                    return;
                }
                fetch("/new?page="+this.pages).then(function (e) {
                    return e.json()
                }).then(function (e) {
                    that.datas=e.concat(that.datas);
                    that.pages++;
                    that.$root.datas=that.datas;
                    obj.over();
                    that.$root.page=that.pages;
                })
            }
        }
    }
</script>

<style scoped>
.mui-table-view{
    /*height: calc(100% - 300px);*/
    overflow-y: scroll;
    border: none;
}
.mui-table-view:after{
    height: 0;
}
</style>


