<template>
    <div class="abso">
        <customHead></customHead>
        <div class="table-box">
            <ul class="mui-table-view" >
                <li class="mui-table-view-cell" v-for="item in datas" >
                    {{item.name}}
                    <a :href="'tel:'+item.phone">{{item.phone}}</a>
                </li>
            </ul>
        </div>

    </div>
</template>

<script>
    import customHead from "@/components/customHead"
    export default {
        name:"Phone",
        data(){
            return {
                datas:[]
            }
        },
        created(){
            var that=this;
            fetch("/memb").then(function (e) {
                return e.json()
            }).then(function (e) {
                that.datas=e
            })
        },
        components:{customHead}
    }
</script>

<style scoped>
    .table-box{

        height: 100vh;
    }
    .table-box ul{
        padding: 44px 0 0;
        height: calc(100%  - 50px);overflow-y: scroll;
    }
</style>


