<template>
    <div style="height: 100vh">
        <div ref="echarts" id="main" style="width: 100%;height: 100%;padding-top: 44px">

        </div>
    </div>

</template>

<script>
    export default {
        name:"Looklog",
        data(){
            return {
                e:[]
            }
        },
        mounted(){
            var that=this;
            var echarts=this.$root.echarts;
            var myChart = echarts.init(document.getElementById('main'));
            fetch("/logNum?mid="+JSON.parse(sessionStorage.indexLogin).mid).then(function (e) {
                return e.json()
            }).then(function (e) {
                myChart.setOption({
                    title: {
                        text: '日志表'
                    },
                    tooltip: {},
                    xAxis: {
                        data: ['已发送', '已接受']
                    },
                    yAxis: {},
                    series: [{
                        name: '销量',
                        type: 'bar',
                        data: e
                    }]
                });
            })

        }
    }
</script>

<style scoped>

</style>


