<template>
    <div class="mui-input-group" style="padding-top: 44px">
        <div class="mui-input-row">
            <label>原始密码：</label>
            <input type="password" class="mui-input-clear mui-input-password" placeholder="请输入原始密码" v-model="pass1">
        </div>
        <div class="mui-input-row">
            <label>新密码：</label>
            <input type="password" class="mui-input-clear mui-input-password" placeholder="请输入密码" v-model="pass2">
        </div>
        <div class="mui-input-row">
            <label>确认密码：</label>
            <input type="password" class="mui-input-clear mui-input-password" placeholder="请输入密码" v-model="pass3">
        </div>
        <div class="mui-button-row">
            <button type="button" class="mui-btn mui-btn-primary" @click="submit">确认</button>
            <button type="button" class="mui-btn mui-btn-danger" >取消</button>
        </div>
    </div>
</template>

<script>

    export default {
        data(){
            return {
                pass1:"",
                pass2:"",
                pass3:"",
            }
        },
        methods:{
            submit(){
                var that=this;
                if(this.pass1==""){
                    mui.alert("原始密码不能为空")
                    return false;
                }else if(this.pass2==""){
                    mui.alert("新密码不能为空")
                    return false
                }else if(this.pass3==""){
                    mui.alert("确认密码不能为空")
                    return false
                }else if(this.pass2!= this.pass3){
                    mui.alert("新密码和确认密码不一致")
                    return false;
                }
                fetch("/member/editpass",{
                    method:"post",
                    headers:{"content-type":"application/x-www-form-urlencoded"},
                    body:`pass1=${this.pass1}&pass2=${this.pass2}&pass3=${this.pass3}&mid=${JSON.parse(sessionStorage.indexLogin).mid}`
                }).then(function (e) {
                    return e.text()
                }).then(function (e) {
                    if(e=="ok"){
                        mui.alert("修改成功,请重新登陆","恭喜你")
                        sessionStorage.removeItem("indexLogin");
                        that.$router.push("/login")
                    }
                })
            }
        }
    }
</script>
<style scoped>

</style>


