<template>
    <div style="text-align: center;">
        <header class="mui-bar mui-bar-nav">
            <span @click="back" class="mui-icon mui-icon-back mui-pull-left"></span>
            <router-link to="/"  class="mui-icon mui-icon-home mui-pull-right"></router-link>
            <h1 class="mui-title">日志</h1>
        </header>
        <div style="margin: 54px 0 10px">
            <router-link to="/log"  class="mui-btn">已发送</router-link>
            <router-link to="/accept"  class="mui-btn">已接受</router-link>
            <router-link to="/write"  class="mui-btn">写日志</router-link>
        </div>
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        methods:{
            back(){
                this.$router.go(-1)
            }
        }
    }
</script>

<style scoped>

</style>


