<template>
    <div style="padding-top: 64px;width: 100%;text-align: center;">
        <el-upload
                class="upload-demo"
                ref="upload"
                name="files"
                :on-success="success"
                :action="'upload?mid='+mid"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="fileList"
                :auto-upload="false">
            <el-input name="mid" v-model="mid" hidden></el-input>
            <el-button slot="trigger" size="small" type="primary">选取图片</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
        </el-upload>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                fileList: [],
                imageUrl:"",
                mid:JSON.parse(sessionStorage.indexLogin).mid
            }
        },
        methods:{
            submitUpload() {
                this.$refs.upload.submit();
            },
            success(res,file){
                this.imageUrl = URL.createObjectURL(file.raw);
                if(res){
                    mui.alert("头像修改成功")
                    var obj=JSON.parse(sessionStorage.indexLogin);
                    obj.photo=res;
                    sessionStorage.setItem("indexLogin",JSON.stringify(obj))
                    this.fileList=[]
                }
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            }
        }
    }
</script>

<style scoped>
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
    .imgBox{
        margin: 20px auto;
        width: 150px;height: 150px;border: 1px solid #ccc;border-radius: 5px;
        font-size: 50px;text-align: center;line-height: 150px;
        position: relative;
    }
    .imgBox>div{
        width: 100%;height: 100%;position: absolute;top: 0;left: 0;
    }
    .imgBox>img{
        width: 100%;height: 100%;position: absolute;top: 0;left: 0;z-index: 2;
    }
    .imgBox>input{
        width: 100%;height: 100%;position: absolute; top: 0;left: 0;opacity: 0;z-index: 1;
    }
</style>


