<template>
    <div>
        <ul class="mui-table-view">
            <li class="mui-table-view-cell" v-for="item in datas">{{item.log}}</li>
        </ul>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                datas:[]
            }
        },
        created(){
            var that=this;
            var mid1=JSON.parse(sessionStorage.indexLogin).mid;
            fetch("/send?mid1="+mid1).then(function (e) {
                return e.json()
            }).then(function (e) {
                that.datas=e;
            })
        }
    }
</script>

<style scoped>

</style>


