import Vue from 'vue'
import Router from 'vue-router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import Index from '@/components/Index'
import Main from '@/components/Main'
import Phone from '@/components/Phone'
import Log from '@/components/Log'

import Set from '@/components/Set'
import Setting from '@/components/Setting'
import Resetpass from '@/components/Resetpass'
import Upphoto from '@/components/Upphoto'
import Looklog from '@/components/Looklog'

import Login from '@/components/Login'
import Send from '@/components/Send'
import Accept from '@/components/Accept'
import Write from '@/components/Write'

Vue.use(Router)
Vue.use(ElementUI)

var router= new Router({
  linkActiveClass:"active",
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children:[
          {path:"",component:Main},
          {path:"/phone",component:Phone},

      ]
    },
    {path:"/log",component:Log,children:[
        {path:"",component:Send},
        {path:"/accept",component:Accept},
        {path:"/write",component:Write}
    ]},
    {path:"/set",component:Set,children:[
        {path:"",component:Setting},
        {path:"/resetpass",component:Resetpass},
        {path:"/upphoto",component:Upphoto},
        {path:"/looklog",component:Looklog},
    ]},
    {path:'/login',component:Login}
  ]
})
router.beforeEach(function (to,from,next) {
    if(to.path=="/"||to.path=="/login"){
      next();
    }else{
      if(sessionStorage.getItem("indexLogin")){
        next()
      }else{
        next("/login")
      }
    }
})
export default router



