<template>
    <nav class="mui-bar mui-bar-tab">
        <router-link to="/" class="list-item" exact>
            <span class="mui-icon mui-icon-home"></span>
            <span class="mui-tab-label">首页</span>
        </router-link>
        <router-link to="/phone" class="list-item">
            <span class="mui-icon mui-icon-phone"></span>
            <span class="mui-tab-label">电话</span>
        </router-link>
        <router-link to="/log" class="list-item">
            <span class="mui-icon mui-icon-email"></span>
            <span class="mui-tab-label">日志</span>
        </router-link>
        <router-link to="/set" class="list-item">
            <span class="mui-icon mui-icon-gear"></span>
            <span class="mui-tab-label">设置</span>
        </router-link>
    </nav>
</template>

<script>
    export default {
        name:"customTab"
    }
</script>

<style scoped>
.list-item{
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}
    .active{
        color:#007aff
    }
</style>


