<template>
    <div>
        <select name="" @change="change" v-model="selected">
            <option value="">请选择接受者</option>
            <option :value="item.mid" v-for="item in datas">{{item.name}}</option>
        </select>
        <textarea name=""  cols="30" rows="10" v-model="log"></textarea>
        <button type="button" class="mui-btn mui-btn-success mui-btn-outlined" @click="submit">提交</button>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                datas:[],
                selected:"",
                mid1:"",
                mid2:"",
                log:"",
            }
        },
        created(){
            var that=this;
            fetch("/mem").then(function (e) {
                return e.json()
            }).then(function (e) {
                that.datas=e;
            })
        },
        methods:{
            change() {
                this.mid1 = JSON.parse(sessionStorage.indexLogin).mid;
                this.mid2 = this.selected;
            },
            submit(){
                fetch("/write",{
                    method:"post",
                    headers:{"content-type":"application/x-www-form-urlencoded"},
                    body:"mid1="+this.mid1+"&mid2="+this.mid2+"&log="+this.log
                }).then(function (e) {
                    return e.text()
                }).then(function (e) {
                    if(e=="ok"){
                        mui.toast('提交成功',{ duration:'long', type:'div' })
                    }
                })
            }
        }
    }
</script>
<style scoped>
option{
    width: 100vw;
}
</style>


