<template>
    <header class="mui-bar mui-bar-nav">
        <router-link to="/" class="mui-icon mui-icon-home mui-pull-left"></router-link>
        <img :src="photo" v-if="login" style="width: 36px;height:36px;border-radius: 50%;margin-top: 4px"  class="mui-pull-right" alt="">
        <router-link to="/login" v-else class="mui-icon mui-icon-personadd  mui-pull-right" ></router-link>
        <h1 class="mui-title">首页</h1>
    </header>
</template>

<script>
    export default {
        name:"customHead",
        data(){
            return {
                login:"",
                name:"",
                photo:""
            }
        },
        created(){
            if(sessionStorage.getItem("indexLogin")){
                this.login=true;
                this.name=JSON.parse(sessionStorage.indexLogin).name;
                this.photo=JSON.parse(sessionStorage.indexLogin).photo;
            }else{
                this.login=false
            }
        }
    }
</script>

<style scoped>

</style>


